.tur-table-mur {
  border-collapse: collapse;
  padding: 10px;
  width: 100%;
  text-align: left;
  background-color: #ffffff;
  color: inherit;
}

.tur-table-mur thead {
  border-bottom: 1px solid #eeeeee;
}

.tur-table-mur th,
.tur-table-mur td {
  padding: 15px;
  border-bottom: 1px solid #eeeeee;
}